import citySelect from '@/components/linzq-citySelect/linzq-citySelect.vue';
import popupLayer from '@/components/popup-layer/popup-layer.vue';
import qqmapsdk from '../../sdk/qqmap-wx-jssdk.js'
const QQMapWX = new qqmapsdk({
	key: 'IFIBZ-YVHCX-HNP4H-TK34M-35AMT-D2FV4'
});
export default {
	components: {
		citySelect,
		popupLayer
	},
	mounted() {
		this.$refs.popupRef.close();
	},
	onReachBottom() {
		var that = this
		this.page++
		uni.showLoading({
			title: "拼命加载中"
		})
		uni.request({
			url: this.$bashUrl + '/index.php/home/Api/home/',
			method: 'POST',
			data: {
				type: 'all',
				page: this.page,
				limit: this.limit
			},
			success: res => {
				// console.log(res)
				uni.hideLoading()
				var lat1 = 0
				var lat2 = 0
				var lng1 = 0
				var lng2 = 0
				uni.getLocation({
					type: 'gcj02',
					geocode: true,
					success: res => {
						// console.log(res)
						lat1 = res.latitude
						lng1 = res.longitude;
					},
					complete: () => {
						QQMapWX.reverseGeocoder({
							location: {
								latitude: lat1,
								longitude: lng1
							},
							success: res => {
								lat1 = res.result.location.lat
								lng1 = res.result.location.lng
							}
						})
						res.data.forEach((item, index) => {
							if (item.lat && item.lng) {
								item.distance = that.$dis(lat1, lng1, Number(item.lat), Number(item.lng))
							}
						})
						if (res.data.length > 0) {
							this.dataList = this.dataList.concat(res.data)
							// console.log(this.dataList)
						} else {
							uni.showToast({
								title: "没有更多数据啦！",
								icon: "none"
							})
						}
					}
				})

			}
		})
	},
	data() {
		return {
			region: '定位中...',
			// 轮播图控制变量
			indicatorDots: true,
			indicatorColor: '#ECECEC',
			indicatorActiveColor: '#3374FF',
			autoplay: true,
			interval: 7000,
			duration: 800,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			dataList: [],
			imgUrl: '',
			hotShop: [],
			// 轮播图指示点
			current: 0,
			current1: 0,
			a: 'item-box',
			imgList: [{
					img: '../../static/imgs/213123123.png',
					xiaobiao: 0
				},
				{
					img: '../../static/imgs/213123123.png',
					xiaobiao: 1
				},
				{
					img: '../../static/imgs/213123123.png',
					xiaobiao: 2
				}
			],
			start: 0,
			startY: 0,
			limit: 20,
			page: 1,
			latitude: 0,
			longitude: 0
		};
	},
	onLoad: function() {
		// console.log(111)
		var that = this
		that.imgUrl = that.$imgUrl
		uni.request({
			url: that.$bashUrl + '/index.php/home/Api/home/',
			method: 'POST',
			data: {
				type: 'all',
				page: that.page,
				limit: that.limit
			},
			success: res => {
				console.log(res)
				var arr = [];
				var lat1 = 0
				var lat2 = 0
				var lng1 = 0
				var lng2 = 0
				uni.getLocation({
					type: 'wgs84',
					geocode: true,
					success: res => {
						// console.log('获取地理位置成功')
						// uni.showLoading({
						// 	title:"获取地理位置成功"
						// })
						// console.log(res)
						lat1 = res.latitude
						lng1 = res.longitude;
					},
					fail:(err)=>{
							// console.log('获取地理位置fail')
							// uni.showLoading({
							// 	title:"fail"
							// })
					},
					complete: () => {
						QQMapWX.reverseGeocoder({
							location: {
								latitude: lat1,
								longitude: lng1
							},
							success: res => {
								// console.log(res)
								lat1 = res.result.location.lat
								lng1 = res.result.location.lng
							}
						})
						// console.log(lat1, lng1)
						res.data.forEach((item, index) => {
							if (item.hot < 6) {
								arr.push(item)
							}
							if (item.lat && item.lng) {
								item.distance = that.$dis(lat1, lng1, Number(item.lat), Number(item.lng))
							}
						})
						that.hotShop = arr.slice(0, 6)
						// console.log(that.hotShop);
						// res.data.forEach((item,index)=>{
						// 	that.getDistance()
						// })
						// let arr1 = [] //营业
						// let arr2 = [] //打样
						// res.data.forEach(item=>{
						// 	if(item.status==10){
						// 		arr1.push(item)
						// 	}else{
						// 		arr2.push(item)
						// 	}
						// })
						// arr1 = arr1.concat(arr2)
						// console.log(arr2)
						that.dataList = res.data;
						
					}
				})
			}
		});
		// success: (res => {
		// 	console.log(res)
		// 	that.dataList = res.data
		// })
	},
	methods: {
		toyaoqing(){
			uni.navigateTo({
				url:"../wallet/wallet"
			})
		},
		// 经纬度转换成三角函数中度分表形式。
		rad(d) {
			return Math.PI * d / 180.0;
		},
		// 根据经纬度计算距离，参数分别为第一点的纬度，经度；第二点的纬度，经度

		totest() {
			// console.log(1)
			uni.navigateTo({
				url: "../count/count" 
				// url: "../decode/decode" 
			})
		},
		oncity: function() {
			var that = this;
			this.$refs.popupRef.show();
		},
		back_city: function(e) {
			// console.log(e);
			if (e !== 'no') {
				this.region = e.cityName;
				uni.setStorageSync("region", this.region)
				this.$refs.popupRef.close();
			} else {
				this.$refs.popupRef.close();
			}
		},
		region_city: function(e) {
			// console.log("region_city" + e);
			this.region = e;
			uni.setStorageSync("region", this.region)
		},
		// 打开二维码
		openCode: function() {
			uni.scanCode({
				success: function(res) {
					// console.log('条码类型：' + res.scanType);
					// console.log('条码内容：' + res.result);
				}
			});
		},
		startx: function(e) {
			e.preventDefault();
			// 记录位置
			(this.start = e.changedTouches[0].pageX), (this.startY = e.changedTouches[0].pageY);
		},
		endx: function(e) {
			// 滑动结束,判断位置
			console.log(this.star);
			e.preventDefault();
			var moveEndX = e.changedTouches[0].pageX;
			var moveEndY = e.changedTouches[0].pageY;
			var X = moveEndX - this.start;
			var Y = moveEndY - this.startY;
			console.log(X);
			console.log(Y);
			if (Math.abs(X) > Math.abs(Y) && X > 0) {
				// console.log('左');
				// 左滑动轮播状态集合，妈的写个轮播图这么费劲，用组件还是有坏处啊，还不如自己写一个
				var map = {
					'0': 0,
					'1': 0,
					'2': 1
				};
				this.current1 = map[this.current1];
			} else if (Math.abs(X) > Math.abs(Y) && X < 0) {
				// console.log('右');
				var map = {
					'0': 1,
					'1': 2,
					'2': 2
				};
				this.current1 = map[this.current1];
			}
		},
		scroll: function(e) {
			this.old.scrollTop = e.detail.scrollTop;
		},
		goTop: function(e) {
			this.scrollTop = this.old.scrollTop;
			this.$nextTick(function() {
				this.scrollTop = 0;
			});
			uni.showToast({
				icon: 'none',
				title: '纵向滚动 scrollTop 值已被修改为 0'
			});
		},
		// 统一存放跳转方法
		TogoodsDatils: function(event) {
			var shopID = event.currentTarget.dataset.id;
			uni.navigateTo({
				url: '/pages/goods-details/goods-details?id=' + shopID
			});
		},
		ToSearch: function() {
			uni.navigateTo({
				url: '/pages/search/search'
			});
		},
		ToSelectAddress: function() {
			uni.navigateTo({
				url: '/pages/select/address'
			});
		},
		nothing(){
			uni.showToast({
				title:"暂无改功能~",
				icon:"none"
			})
		},
		Toshoping: function(event) {
			var currentID = event.currentTarget.dataset.id;
			uni.navigateTo({
				url: '/pages/shoplist/shoplist?typeid=' + currentID,
				animationType: 'pop-in',
				animationDuration: 200
			});
		},
	}
};
